



























import { defineComponent } from "@vue/composition-api";
import { AbilityScoresObject, Ability } from "@/modules/types";

export default defineComponent({
  props: {
    abilityScores: {
      type: Object as () => AbilityScoresObject
    }
  },
  name: "EditAbilityScores",
  setup(props, vm) {
    const increment = function(ability: Ability) {
      vm.emit("modify", { ability, shift: 1 });
    };

    const decrement = function(ability: Ability) {
      vm.emit("modify", { ability, shift: -1 });
    };

    return {
      enteredAbilities: props.abilityScores,
      increment,
      decrement
    };
  }
});
