





















































import { defineComponent, reactive, ref } from "@vue/composition-api";
import EditAbilityScores from "@/components/EditAbilityScores.vue";
import Footer from "@/components/Footer.vue";
import AppBar from "@/components/AppBar.vue";
import ChoiceList from "@/components/ChoiceList.vue";
import { Ability } from "@/modules/types";

import classicFantasy from "@/modules/sourceClassicFantasy";
import advancedFantasy from "@/modules/sourceAdvancedFantasy";

export default defineComponent({
  name: "App",
  components: { EditAbilityScores, Footer, AppBar, ChoiceList },
  setup(props, vm) {
    vm.root.$vuetify.theme.dark = true;

    const enteredAbilities = reactive({
      str: 10,
      int: 10,
      wis: 10,
      dex: 10,
      con: 10,
      cha: 10
    });
    const eaMod = function(ability: Ability, shift: number) {
      enteredAbilities[ability] += shift;
      enteredAbilities[ability] = Math.min(18, enteredAbilities[ability]);
      enteredAbilities[ability] = Math.max(3, enteredAbilities[ability]);
    };
    const eaReset = function() {
      enteredAbilities.str = 10;
      enteredAbilities.int = 10;
      enteredAbilities.wis = 10;
      enteredAbilities.dex = 10;
      enteredAbilities.con = 10;
      enteredAbilities.cha = 10;
    };
    const dice = function() {
      return 1 + Math.floor(Math.random() * 6);
    };
    const eaRandom = function() {
      enteredAbilities.str = dice() + dice() + dice();
      enteredAbilities.int = dice() + dice() + dice();
      enteredAbilities.wis = dice() + dice() + dice();
      enteredAbilities.dex = dice() + dice() + dice();
      enteredAbilities.con = dice() + dice() + dice();
      enteredAbilities.cha = dice() + dice() + dice();
    };
    const sourceChanger = ref(false);

    const sources = reactive({
      sources: [
        {
          name: "Classic Fantasy Genre Rules",
          source: classicFantasy,
          active: true
        },
        {
          name: "Advanced Fantasy Genre Rules",
          source: advancedFantasy,
          active: false
        }
      ]
    });

    return {
      enteredAbilities,
      eaMod,
      eaReset,
      eaRandom,
      sources,
      sourceChanger
    };
  }
});
