































import { defineComponent } from "@vue/composition-api";
export default defineComponent({
  name: "AppBar",
  setup(props, vm) {
    const reset = function() {
      vm.emit("reset");
    };
    const randomize = function() {
      vm.emit("randomize");
    };
    const sourcemenu = function() {
      vm.emit("sourcemenu");
    };
    return {
      reset,
      randomize,
      sourcemenu
    };
  }
});
