import { standardPr } from "@/modules/standardPr";
import { secondaryPr } from "@/modules/secondaryPr";
import { halflingPr } from "@/modules/halflingPr";
import { standardGenerator } from "@/modules/standardGenerator";
import { standardRequirements } from "@/modules/standardRequirements";
import { noStrGenerator } from "@/modules/noStrGenerator";

export default function() {
  return {
    generators: {
      CORE_STR: standardGenerator(new Set(["str"])),
      CORE_INT: standardGenerator(new Set(["int"])),
      CORE_WIS: standardGenerator(new Set(["wis"])),
      CORE_STR_INT: standardGenerator(new Set(["str", "int"])),
      CORE_STR_DEX: standardGenerator(new Set(["str", "dex"])),
      NSTR_DEX: noStrGenerator(new Set(["dex"]))
    },
    classes: {
      Cleric: {
        requirements: standardRequirements([]),
        primeRequisites: standardPr("wis"),
        checkAgainst: "CORE_WIS"
      },
      Dwarf: {
        requirements: standardRequirements(["con"]),
        primeRequisites: standardPr("str"),
        checkAgainst: "CORE_STR"
      },
      Elf: {
        requirements: standardRequirements(["int"]),
        primeRequisites: secondaryPr("int", "str", 13, 13), // write tomorroow
        checkAgainst: "CORE_STR_INT"
      },
      Fighter: {
        requirements: standardRequirements([]),
        primeRequisites: standardPr("str"),
        checkAgainst: "CORE_STR"
      },
      Halfling: {
        requirements: standardRequirements(["con", "dex"]),
        primeRequisites: halflingPr(),
        checkAgainst: "CORE_STR_DEX"
      },
      "Magic-User": {
        requirements: standardRequirements([]),
        primeRequisites: standardPr("int"),
        checkAgainst: "CORE_INT"
      },
      Thief: {
        requirements: standardRequirements([]),
        primeRequisites: standardPr("dex"),
        checkAgainst: "NSTR_DEX"
      }
    }
  };
}
