






























import { defineComponent, computed } from "@vue/composition-api";
import {
  AbilityScoresObject as ASO,
  Source,
  AbilityGenerator
} from "@/modules/types";
export default defineComponent({
  name: "ChoiceList",
  props: {
    abilityScores: {
      type: Object as () => ASO
    },
    sources: {
      type: Object as () => {
        sources: { name: string; active: boolean; source: Source }[];
      }
    }
  },
  setup(props) {
    const enteredAbilities = props.abilityScores as ASO;
    const sources = (props.sources as {
      sources: { name: string; active: boolean; source: Source }[];
    }).sources;

    const activeSources = computed(() => {
      return sources.filter(source => source.active);
    });
    const generators = computed(() => {
      return activeSources.value.reduce((acc, cur) => {
        const source = cur.source();
        return Object.assign(acc, source.generators) as {
          [key: string]: AbilityGenerator;
        };
      }, {});
    });
    const generatorOutputs = computed(() => {
      const gens = generators.value as { [key: string]: AbilityGenerator };
      return Object.keys(gens).reduce(
        (acc, gen) =>
          Object.assign(acc, {
            [gen]: Array.from(gens[gen](enteredAbilities))
          }),
        {} as {
          [key: string]: { mod: number; scores: ASO }[];
        }
      );
    });
    const classOptions = computed(() => {
      return activeSources.value.reduce(
        (acc, cur) => {
          const source = cur.source();
          return Object.assign(acc, source.classes);
        },
        {} as {
          [key: string]: {
            requirements: (i: ASO) => boolean;
            primeRequisites: (i: ASO) => number;
            checkAgainst: string;
          };
        }
      );
    });

    const tableHeaders = [
      {
        text: "Class",
        sortable: false,
        value: "className",
        divider: true
      },
      {
        text: "XP Bonus",
        align: "end",
        value: "prBonus"
      },
      {
        text: "Score Loss",
        align: "start",
        value: "adjustments",
        divider: true
      },
      {
        text: "STR",
        value: "str",
        sortable: false,
        align: "center"
      },
      {
        text: "INT",
        value: "int",
        sortable: false,
        align: "center"
      },
      {
        text: "WIS",
        value: "wis",
        sortable: false,
        align: "center"
      },
      {
        text: "DEX",
        value: "dex",
        sortable: false,
        align: "center"
      },
      {
        text: "CON",
        value: "con",
        sortable: false,
        align: "center"
      },
      {
        text: "CHA",
        value: "cha",
        sortable: false,
        align: "center"
      }
    ];

    const tableData = computed(() => {
      return Object.keys(classOptions.value)
        .flatMap(className => {
          const checker = classOptions.value[className];
          return generatorOutputs.value[checker.checkAgainst].map(generated => {
            return {
              className: className,
              prBonus: checker.primeRequisites(generated.scores),
              adjustments: generated.mod,
              req: checker.requirements(generated.scores),
              ...generated.scores
            };
          });
        })
        .filter(option => option.req);
    });
    return {
      tableHeaders,
      tableData
    };
  }
});
