import { Ability, AbilityScoresObject as ASO } from "@/modules/types";

export function secondaryPr(
  primary: Ability,
  secondary: Ability,
  t1: number | null,
  t2: number,
  sym = false
): (scores: ASO) => number {
  const t1n = t1 === null ? 3 : t1;
  return function(scores: ASO): number {
    if (scores[primary] >= 16 && scores[secondary] >= t2) {
      return 10;
    } else if (sym && scores[secondary] >= 16 && scores[primary] >= t2) {
      return 10;
    } else if (scores[primary] >= 13 && scores[secondary] >= t1n) {
      return 5;
    } else if (sym && scores[secondary] >= 13 && scores[primary] >= t1n) {
      return 5;
    } else {
      return 0;
    }
  };
}
