import { standardPr } from "@/modules/standardPr";
import { secondaryPr } from "@/modules/secondaryPr";
import { standardGenerator } from "@/modules/standardGenerator";
import { noStrGenerator } from "@/modules/noStrGenerator";
import { standardRequirements } from "@/modules/standardRequirements";

export default function() {
  return {
    generators: {
      CORE_STR: standardGenerator(new Set(["str"])),
      CORE_INT: standardGenerator(new Set(["int"])),
      CORE_WIS: standardGenerator(new Set(["wis"])),
      CORE_CHA: standardGenerator(new Set(["cha"])),
      CORE_STR_INT: standardGenerator(new Set(["str", "int"])),
      CORE_STR_WIS: standardGenerator(new Set(["str", "wis"])),
      CORE_STR_DEX: standardGenerator(new Set(["str", "dex"])),
      CORE_STR_CON: standardGenerator(new Set(["str", "con"])),
      CORE_INT_DEX: standardGenerator(new Set(["int", "dex"])),
      NSTR_DEX: noStrGenerator(new Set(["dex"]))
    },
    classes: {
      "Acrobat (Adv.)": {
        requirements: standardRequirements([]),
        primeRequisites: standardPr("dex"),
        checkAgainst: "NSTR_DEX"
      },
      "Assassin (Adv.)": {
        requirements: standardRequirements([]),
        primeRequisites: standardPr("dex"),
        checkAgainst: "NSTR_DEX"
      },
      "Barbarian (Adv.)": {
        requirements: standardRequirements(["con"]),
        primeRequisites: secondaryPr("con", "str", null, 16, true),
        checkAgainst: "CORE_STR_CON"
      },
      "Bard (Adv.)": {
        requirements: standardRequirements(["dex", "int"]),
        primeRequisites: standardPr("cha"),
        checkAgainst: "CORE_CHA"
      },
      "Drow (Adv.)": {
        requirements: standardRequirements(["int"]),
        primeRequisites: secondaryPr("wis", "str", 13, 13),
        checkAgainst: "CORE_STR_WIS"
      },
      "Druid (Adv.)": {
        requirements: standardRequirements([]),
        primeRequisites: standardPr("wis"),
        checkAgainst: "CORE_WIS"
      },
      "Duergar (Adv.)": {
        requirements: standardRequirements(["con", "int"]),
        primeRequisites: standardPr("str"),
        checkAgainst: "CORE_STR"
      },
      "Gnome (Adv.)": {
        requirements: standardRequirements(["con"]),
        primeRequisites: secondaryPr("int", "dex", 13, 13),
        checkAgainst: "CORE_INT_DEX"
      },
      "Half-Elf (Adv.)": {
        requirements: standardRequirements(["cha", "con"]),
        primeRequisites: secondaryPr("str", "int", 13, 13, true),
        checkAgainst: "CORE_STR_INT"
      },
      "Half-Orc (Adv.)": {
        requirements: standardRequirements([]),
        primeRequisites: secondaryPr("str", "dex", 13, 16, true),
        checkAgainst: "CORE_STR_DEX"
      },
      "Illusionist (Adv.)": {
        requirements: standardRequirements(["dex"]),
        primeRequisites: standardPr("int"),
        checkAgainst: "CORE_INT"
      },
      "Knight (Adv.)": {
        requirements: standardRequirements(["con", "dex"]),
        primeRequisites: standardPr("str"),
        checkAgainst: "CORE_STR"
      },
      "Paladin (Adv.)": {
        requirements: standardRequirements(["cha"]),
        primeRequisites: secondaryPr("str", "wis", null, 16, true),
        checkAgainst: "CORE_STR_WIS"
      },
      "Ranger (Adv.)": {
        requirements: standardRequirements(["con", "wis"]),
        primeRequisites: standardPr("str"),
        checkAgainst: "CORE_STR"
      },
      "Svirfneblin (Adv.)": {
        requirements: standardRequirements(["con"]),
        primeRequisites: standardPr("str"),
        checkAgainst: "CORE_STR"
      }
    }
  };
}
