import { AbilityScoresObject as ASO, Ability } from "@/modules/types";

export function standardGenerator(primes: Set<Ability>) {
  return function*(i: ASO) {
    const strVar = Math.max(0, i.str - 9);
    const intVar = Math.max(0, i.int - 9);
    const wisVar = Math.max(0, i.wis - 9);

    for (let strI = 0; strI <= strVar; strI++) {
      for (let intI = 0; intI <= intVar; intI++) {
        for (let wisI = 0; wisI <= wisVar; wisI++) {
          let poolVar = strI + intI + wisI;
          if (poolVar % 2 != 0) {
            continue;
          }
          poolVar /= 2;

          for (
            let strB = 0;
            strB <= poolVar && (strB === 0 || primes.has("str"));
            strB++
          ) {
            const poolStr = poolVar - strB;
            for (
              let intB = 0;
              intB <= poolStr && (intB === 0 || primes.has("int"));
              intB++
            ) {
              const poolInt = poolStr - intB;
              for (
                let wisB = 0;
                wisB <= poolInt && (wisB === 0 || primes.has("wis"));
                wisB++
              ) {
                const poolWis = poolInt - wisB;
                for (
                  let dexB = 0;
                  dexB <= poolWis && (dexB === 0 || primes.has("dex"));
                  dexB++
                ) {
                  const poolDex = poolWis - dexB;
                  for (
                    let conB = 0;
                    conB <= poolDex && (conB === 0 || primes.has("con"));
                    conB++
                  ) {
                    const chaB = poolDex - conB;
                    if (
                      (chaB > 0 && !primes.has("cha")) ||
                      (strI > 0 && strB > 0) ||
                      (intI > 0 && intB > 0) ||
                      (wisI > 0 && wisB > 0)
                    ) {
                      break;
                    }
                    const scores = {
                      str: i.str - strI + strB,
                      int: i.int - intI + intB,
                      wis: i.wis - wisI + wisB,
                      dex: i.dex + dexB,
                      con: i.con + conB,
                      cha: i.cha + chaB
                    };
                    if (
                      scores.str > 18 ||
                      scores.int > 18 ||
                      scores.wis > 18 ||
                      scores.dex > 18 ||
                      scores.con > 18 ||
                      scores.cha > 18
                    ) {
                      break;
                    }
                    yield {
                      scores,
                      mod: poolVar
                    };
                  }
                }
              }
            }
          }
        }
      }
    }
  };
}
