import { Ability, AbilityScoresObject as ASO } from "@/modules/types";

export function standardPr(ability: Ability): (scores: ASO) => number {
  return function(scores: ASO): number {
    if (scores[ability] >= 16) {
      return 10;
    } else if (scores[ability] >= 13) {
      return 5;
    } else if (scores[ability] >= 9) {
      return 0;
    } else if (scores[ability] >= 6) {
      return -10;
    } else {
      return -20;
    }
  };
}
