import { AbilityScoresObject as ASO } from "@/modules/types";

export function halflingPr(): (scores: ASO) => number {
  return function(scores: ASO): number {
    if (scores["str"] >= 13 && scores["dex"] >= 13) {
      return 10;
    } else if (scores["str"] >= 13 || scores["dex"] >= 13) {
      return 5;
    } else {
      return 0;
    }
  };
}
